@tailwind base;
@tailwind components;
@tailwind utilities;

header ul li {
    @apply px-2
}

.bg-primary{
    background: #5551E3;
}
.bg-secondary{
    background: #2b2d77;  
}

.mobile-nav{
    @apply fixed top-10 left-0 bg-gray-800 w-full h-1/4;
}

.mobile-nav li{
    @apply w-full h-full text-center border-b-2;
}

.project-disc{
    @apply absolute left-0 right-0 bottom-0  top-0 bg-[#2b2d77] opacity-0 duration-500 hover:opacity-100 
}

.btn{
    @apply bg-[#2b2d77] px-5 py-2 font-bold border-2 hover:border-[white] rounded;
}
html{
    scroll-behavior: smooth;
}